@font-face {
  font-family: 'KPLSynt';
  src: url('../fonts/KPLSyntTrial-Headline.woff');
  font-weight: 400;
}
@font-face {
  font-family: 'KPLSynt';
  src: url('../fonts/KPLSynt-Regular.woff');
  font-weight: 300;
  font-style: normal;
}
@font-face {
 font-family: 'DinSynt';
 src: url('../fonts/DinamoSynt-Regular.woff');
 font-weight: 300;
 font-style: normal;
}
@font-face {
  font-family: 'KPLSynt';
  src: url('../fonts/KPLSynt-Italic.woff');
  font-weight: 300;
  font-style: italic;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-weight: normal;
  -webkit-font-smoothing: antialiased;
}

html {
  font-size: 1.1vw;
  @media (max-width: 670px) {
    font-size: 18px;
  }
  --font-body: 1.8rem;
  --font-mid: 2.8rem;
  --font-icon: 7rem;
  --font-lge: 9.25vw;
  --font-small: 0.85rem;
}

body {
  font-family: 'KPLSynt', serif;
  font-style: 300;
  line-height: 1.15;
  .about main {
    margin-top: 6rem;
    max-width: 65%;
    margin: 7rem auto 7rem;
  }
}

.hide {
  display: none;
}

em, i {
  font-style: italic;
  font-style: 300;
}

.pane {
  width: 100vw;
  height: 100vh;
  background: white;
  position: fixed;
  z-index: 250;
  display: none;
  &.open {
    display: block;
  }
  &-inner {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    figure {
      max-width: 60%;
      img {
        max-width: 100%;
        display: block;
      }
    }
  }
}

.pane-close {
  font-size: var(--font-body);
  font-weight: 400;
  position: fixed;
  top: 1rem; right: 1rem;
  color: black;
  text-transform: uppercase;
  line-height: 1;
  z-index: 250;
}

figcaption {
  font-size: 1rem;
  margin-top: 1rem;
  text-align: center;
  line-height: 1.1;
  a {
    text-transform: none;
    border-bottom: 1px solid;
  }
}

header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: fixed;
  width: calc(100vw - 2rem);
  left: 1rem; top: 1rem;
  z-index: 200;

  h1, a {
    // flex: 1;
  }
  h1 {
    font-family: 'KPLSynt', serif;
    font-weight: 400;
    font-size: var(--font-body);
    text-transform: none;
    /* -webkit-font-smoothing: antialiased; */
  }
}

@media (max-width: 670px) {
  dl {
    margin-bottom: 1.1em;
  }
  .people {
    flex-direction: column !important;
  }
  .call p:last-of-type {
    max-width: 100% !important;
    font-size: 0.65rem !important;
  }
}

[data-template="home"] {
  padding-top: 6.6em;
  max-width: 58%;
  margin: 0 auto;
  padding-bottom: 4.4em;
}
[data-template="essay"] h2 {
  font-size: var(--font-body);
}
[data-template="essay"] p {
  padding-left: 0;
}
p, h3 {
  font-size: 1.8rem;
  font-weight: 400;
  // margin: 0 auto;
}
p, h1, h2, figcaption, .footnotes {
  font-family: 'DinSynt';
}
h3 + p, p + h3 {
  margin-top: 1em;
}
sup {
  font-size: 0.8rem;
  line-height: 1;
  + sup {
    margin-left: -0.25em;
  }
}

nav {
  font-size: var(--font-body);
  font-weight: 400;
  display: inline-block;
  justify-content: flex-end;
  text-transform: uppercase;
  text-align: right;
}

a {
  color: black;
  text-decoration: none;
  text-transform: uppercase;
}

p {

  & + p {
    margin-top: 1em;
  }
  span {
    display: none;
  }
  a {
    text-decoration: none;
    color: black;
    display: inline;
    text-transform: none !important;
    line-height: 1.2;
    &:hover {
      border-bottom: 1px solid;
    }
    & + h2 {
      margin-top: 4.4em;
    }
  }
  &:hover span {
    display: block !important;
  }
}

h2 {
  font-size: 2.5vw;
  line-height: 1.2;
  margin: 1.1em 0 1.1em 0;
  text-transform: uppercase;
  text-align: center;
  text-indent: 0 !important;
  // max-width: 60%;
}

h3 {
  /* font-family: 'KPLSynt', serif; */
  text-align: center;
  font-weight: 300;
}

blockquote {
  margin: 2.2em 4em 2.2em 12em;
  max-width: 620px !important;
}
main h1 {
  font-size: 9.25vw;
  font-weight: 300;
  line-height: 0.85;
  text-align: center;
  text-transform: uppercase;
  -webkit-column-count: 1;
  width: 100%;
  max-width: 90vw;
  &:not(:last-of-type) {
    margin-bottom: 0.5em;
  }
}

.title-block {
  background: #f7f7f7;
  padding-bottom: 4em;
  height: 100vh;
  position: relative;

  h1 {
    font-family: 'KPLSynt', serif;
    font-weight: 400;
    margin: 0 auto;
    text-transform: none;
    position: absolute;
    top: 50%; left: 50%;
    transform: translate(-50%, -50%);
  }
}

.title, .author {
  text-transform: uppercase;
  font-weight: 300;
}
.text-block .title,
.text-block .subtitle {
  font-size: var(--font-mid);
  text-align: center;
  line-height: 1.05;
  display: block;
}

.text-block {
  // max-width: 940px;
  // margin: 0 auto;
  padding: 6.6em 4em;

  .title {
    margin-bottom: 0.5em;
  }
  .subtitle {
    max-width: 100%;
    margin: 0 0 1.5em 0;
  }
  h2 {
    text-indent: 3em;
  }
  p, p + p, h2, h3 {
    max-width: 860px;
  }
  .subtitle {
    max-width: 820px;
    margin: 0 auto;
    margin-bottom: 2em;
  }
  p + p {
    text-indent: 3em;
    margin: 0;
  }
  figure {
    display: table;
    max-width: 680px;
    margin: 6em auto;
    &:hover img {
      filter: grayscale(0);
    }
    img {
      max-width: 100%;
      margin: 0 auto;
      display: block;
      filter: grayscale(1);
      // position: absolute;
      // top: 50%; left: 50%;
      // transform: translate(-50%, -50%);
    }
    figcaption {
      display: table-caption;
      caption-side: bottom;
      text-align: center;
      line-height: 1.2;
    }
  }
  @media (max-width: 670px) {
    max-width: 100%;
    margin: 0 auto;
    padding-top: 6.6em;
  }
}

.footnotes {
  margin: 2.2em 0;
  margin-left: 1.5em;
  column-count: 2;
  column-gap: 60px;
  hr {
    display: none;
  }
  ol li p {
    font-size: 1.1rem;
    break-inside: avoid;
  }
}

[data-template="about"] .text-block {
  padding-bottom: 8em;
}


dl {
  font-family: 'DinSynt';
  font-size: var(--font-body);
  line-height: 1.1;
  break-inside: avoid;
  dt {
    text-transform: uppercase;
  }
  dd + dt {
    margin-top: 1.1em;
  }
}


header a:hover,
.pane-close:hover {
  cursor: pointer;
  border-bottom: 1px solid;
}

.pane-inner p {
  max-width: 100%;
}

ul {
  list-style: none;
  margin-left: 4rem;
  margin-top: 1em;
  li {
    font-size: 1.8rem;
    line-height: 1.1;
    font-weight: 400;
    &:first-of-type {
      text-transform: uppercase;
    }
  }
}

figure.wipe {
  position: absolute;
  top: 0; left: 0;
  width: 100%; height: 100%;
  z-index: 3;
  background-size: 52vh;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  pointer-events: none;
  transform: translateZ(0);
}

hr {
  display: none;
}

.people {
  max-width: 80%;
  margin-top: 2em;
  margin-left: 4em;
  display: flex;
  flex-direction: row;
  dl:first-of-type {
    margin-right: 2em;
  }
}

@media (max-width: 640px) {
  figure.wipe {
    z-index: 5;
    background-attachment: initial;
  }
}

section {
  display: flex;
  flex-direction: column;
}
section.spacer {
  height: 100vh;
}
section.initial {
  position: -webkit-sticky;
  position: sticky;
  top: 0; height: 100vh;
  left: 0; width: 100vw;
  align-items: center;
  justify-content: center;
}
section.call {
  position: relative;
  top: 0;
  /* padding: 8rem 0; */
  justify-content: center;
  align-items: center;
  background: white;
  height: 100vh;
}

header {
  h1 {
    display: flex;
    align-items: flex-start;
    nav {
      height: 1.2em;
      overflow-y: hidden;
      flex: none;
      text-align: left;
      transition: height ease-in-out 0.5s;
      &.hover {
        height: auto;
      }
      &:hover {
        cursor: pointer;
      }
      .issue {
        display: table;
        padding-left: 0.25rem;
        border-bottom: 1px solid transparent;
        a {
          text-transform: none !important;
          border-bottom: 0 !important;
        }
        &:hover {
          border-bottom: 1px solid !important;
        }
      }
    }
  }
}

[data-template="issue"] {
  .title-block {
    background: white;
    padding-top: 8em;
    h1 {
      font-size: var(--font-mid);
      position: static !important;
      top:inherit;
      left: 0;
      transform: none;
    }
  }
  .issue-items {
    margin-bottom: 4em;
    ul {
      font-size: var(--font-mid);
      li {
        font-size: inherit;
        a {
          text-transform: none;
        }
      }
    }
  }
  h1 {
    font-weight: 300;
  }
  .title {
    // text-transform: none !important;
  }
}

figure {
  &:hover {
    cursor: pointer;
  }
  position: relative;
  &.expand {
    background: black;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: space-around;
    top: 0; left: 0; right: 0; bottom: 0;
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    margin: 0;
    z-index: 210;
    img {
      padding: 4em 0;
    }
    .figure-close {
      display: block;
    }
  }
}

.abstract {
  .abstract-trigger {
    &:hover {
      cursor: pointer;
    }
  }
  .abstract-content {
    font-family: 'DinSynt';

    padding-right: 4em;
    margin-bottom: 3.3em;
    margin-top: 1em;
    margin-left: 5.5em;
    max-width: 70%;
    p {
      font-size: 1.4rem;
      & + p {
        text-indent: 0 !important;
      }
    }

    display: none;
  }
}

.author {
  text-transform: none !important;
}

.menu-trigger {
  &:hover {
    border-bottom: 1px solid !important;
    cursor: pointer;
  }
}

.inline-footnote__num {
  display: block;
  margin-left: 2em;
  text-indent: 0;
}

.inline-footnotes {
  .inline-footnote {

    &__num {
      display: block;
      margin-left: 2em;
      text-indent: 0;
    }

  }
  &__container {
    font-size: var(--font-small);
    width: 100%;
    max-width: 17vw;
    position: absolute;
    top: 0;
    left: 75vw;
    padding-right: 1em;
  }
}
blockquote .inline-footnotes__container {
  left: calc(75vw - 14em);
}

p span {
  display: block !important;
}

[data-display="home"] .text-block {
  padding-bottom: 0 !important;
}



@media (max-width: 736px) {
  .pane-inner figure {
    max-width: 57%;
    margin-top: 3rem;
  }
  body.about main {
    margin-top: 6rem;
    max-width: 85%;
  }
}
@media (max-width: 670px) {
  p, h1, h3, header h1, header a, ul li, .pane-close, dd, dt , dl {
    font-size: 1rem;
  }
  figcaption {
    font-size: 0.7rem;
    line-height: 1.2;
  }
  header h1 {
    flex: 1.5;
    line-height: 1.2;
  }
  header nav {
    flex: 1;
    margin-top: -0.5rem;
  }
  header h1 span {
    display: block;
  }
  p, h3 {
    max-width: 90%;
  }
  figure.wipe {
    background-size: 30vh;
  }
  .pane-inner figure {
    max-width: 58%;
    margin-top: 1rem;
  }
  body.about main {
    margin-top: 6rem;
    max-width: 95%;
  }
}

@media (max-width: 568px) {
  .pane-inner figure {
    max-width: 57%;
    margin-top: 3rem;
  }
}

@media (max-width: 480px) {
  .pane-inner figure {
    max-width: 90%;
    margin-top: 3rem;
  }
  figcaption {
    font-size: 0.6rem;
    line-height: 1.2;
    position: fixed;
    bottom: 1rem;
    left: 1rem;
    right: 0.5rem;
    text-align: left;
  }
}

@import './print.scss';
